.main-wrapper {
  /* background: radial-gradient(ellipse at bottom, #a90e0e 5%, #4e0303 55%, #330000 78%); */
  position: fixed;
  display: flex;
  align-items: flex-end;
  justify-content: flex-start;
  bottom: 0;
  left: 125px;
  margin: 0;
  padding: 0;
}
.tree-container {
  display: block;
  height: 370px;
  margin: 0;
  padding: 0;
}
.text-container {
  margin: 0;
  padding: 0;
  position: absolute;
  bottom: 14%;
  left: 15%;
  right: 15%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  text-align: center;
}
.text-container h2 {
  color: rgba(252, 251, 233, 0.85);
  font-family: 'Great Vibes', cursive;
  font-weight: 400;
  margin: 0 0 0.8em 0;
}
.text-container h2.happy {
  font-size: 3.3em;
}
.text-container a.c2 {
  display: block;
  font-size: 13px;
  width: 40px;
  height: 40px;
  background-color: rgba(252, 251, 233, 0.8);
  border-radius: 50%;
  color: #8c1313;
  text-decoration: none;
  line-height: 40px;
}
.star {
  border-radius: 0;
  border-color: #f7b93f transparent transparent transparent;
  border-style: solid;
  border-top-width: 9.23076923px;
  border-right-width: 12px;
  border-left-width: 12px;
  height: 0;
  width: 0;
  margin-bottom: 5px;
  background: transparent;
  z-index: 100;
  animation: pulsate-star 1.4s ease-in-out infinite alternate-reverse;
}
.star:before,
.star:after {
  border-color: #f7b93f transparent transparent transparent;
  border-style: solid;
  border-top-width: 9.23076923px;
  border-right-width: 12px;
  border-left-width: 12px;
  border-bottom: 0;
  content: '';
  display: block;
  height: 0;
  left: -12px;
  position: absolute;
  top: -9.23076923px;
  width: 0;
}
.star:before {
  -webkit-transform: translate(15.5555555%, -15.5555555%) rotate(72deg) translateY(50%);
          transform: translate(15.5555555%, -15.5555555%) rotate(72deg) translateY(50%);
}
.star:after {
  -webkit-transform: translate(-15.5555555%, -15.5555555%) rotate(-72deg) translateY(50%);
          transform: translate(-15.5555555%, -15.5555555%) rotate(-72deg) translateY(50%);
}
.spiral-container {
  width: 100%;
  height: 100%;
  display: block;
}
.spiral {
  position: relative;
  margin: 0;
  padding: 0;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
}
.spiral.one {
  -webkit-animation: rotate-tree 6s infinite linear both;
          animation: rotate-tree 6s infinite linear both;
  -webkit-animation-delay: -1.7s;
          animation-delay: -1.7s;
}
.spiral.one .light {
  background: #fcfbe9;
}
.spiral.two {
  -webkit-animation: rotate-tree 7s infinite linear both;
          animation: rotate-tree 7s infinite linear both;
  -webkit-animation-delay: -1s;
          animation-delay: -1s;
}
.spiral.two .light {
  background: #e8a41d;
}
.spiral.three {
  -webkit-animation: rotate-tree 8s infinite linear both;
          animation: rotate-tree 8s infinite linear both;
  -webkit-animation-delay: -0.1s;
          animation-delay: -0.1s;
}
.spiral.three .light {
  background: #f5e393;
}
.spiral > .light-wrapper {
  border-radius: 50%;
  position: absolute;
  display: block;
  width: 6px;
  height: 6px;
  left: 50%;
  top: 50%;
  margin: -3px 0 0 -3px;
}
.spiral .stabilise {
  position: absolute;
  display: block;
  width: 100%;
  height: 100%;
}
.spiral .light {
  position: absolute;
  display: block;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  border-radius: 50%;
  opacity: 1;
  -webkit-animation-name: pulsate;
          animation-name: pulsate;
  -webkit-animation-timing-function: ease-in-out;
          animation-timing-function: ease-in-out;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  animation-direction: alternate-reverse;
}
