body {
  /* background-color: #136b99; */
  background: rgb(19, 107, 153);
  background: -moz-linear-gradient(
    90deg,
    rgba(19, 107, 153, 1) 0%,
    rgba(19, 162, 191, 1) 100%
  );
  background: -webkit-linear-gradient(
    90deg,
    rgba(19, 107, 153, 1) 0%,
    rgba(19, 162, 191, 1) 100%
  );
  background: linear-gradient(
    90deg,
    rgba(19, 107, 153, 1) 0%,
    rgba(19, 162, 191, 1) 100%
  );
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#136b99",endColorstr="#13a2bf",GradientType=1);
  color: #fff;
}

